.code-highlight {
  background: #f9f9f9;
  border-radius: 8px;
  padding: 0 15px;
  overflow: hidden;
}

/*
 * GitHub style for Pygments
 * Courtesy of GitHub.com
 */
.code-highlight .hll {
  background-color: #f8f8f8;
  border: 1px solid #ccc;
  padding: 6px 10px;
  border-radius: 3px;
}
.code-highlight .c {
  color: #999988;
  font-style: italic;
}
.code-highlight .err {
  color: #a61717;
  background-color: #e3d2d2;
}
